import { Component } from 'substance'

class Preface extends Component {
  render($$) {
    let preface = this.props.preface
    let el = $$('section').addClass('sc-preface')

    el.append(
      $$('div').addClass('se-content')
        .setInnerHTML(preface.content)
    )

    return el
  }
}

export default Preface